body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  background-color: #1c2b35;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header img {
  padding-left: 140px;
}
.sub-header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 140px;
}
.sub-header a {
  cursor: pointer;
  margin: 15px;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  font-size: 17px;
}

.cart {
  position: -webkit-sticky;
  position: sticky;
  top: 0%;
  padding: 25px;
}
.cart h5 {
  margin-top: 20px;
  font-size: 20px;
  line-height: 20px;
  text-align: justify;
}
.btn-container {
  margin-top: 25px;
}

.cart-btn:nth-child(1) {
  background-color: #ff3030;
}

.cart-btn:nth-child(2) {
  background-color: #ff9900;
}

.cart-btn {
  border: none;
  color: #fff;
  font-size: 17px;
  padding: 20px;
  margin: 15px 0;
  width: 75%;
  font-weight: 700;
  cursor: pointer;
}
.fa-icon {
  margin-left: 10px;
}
.cart-btn:hover {
  background-color: rgba(242, 242, 242, 0.84);
  color: black;
}

.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 510px;
  width: 300px;
  border: 1px solid gray;
  border-radius: 8px;
}
.product img {
  height: 286px;
  width: 286px;
  margin: 7px;
  border-radius: 10px;
}

.product-info {
  margin: 10px 25px;
}

.main-btn {
  background-color: #ffe0b3;
  border: none;
  width: 100%;
  height: 48px;
  border-radius: 0 0 8px 8px;
}

.main-btn:hover {
  background-color: orange;
  cursor: pointer;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container {
  display: grid;
  grid-template-columns: 4fr 1fr;
}

.cart-container {
  text-align: center;
  background-color: rgba(255, 153, 0, 0.3);
}

.products-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 45px;
  font-family: "lato";
  font-weight: 400;
  margin: 100px 50px;
}

@media screen and (max-width: 1080px) {
  .products-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

